@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: black;
  font-family: 'DM Sans', sans-serif;
  overflow-x: hidden;
}

*:not(.bg-white) {
  color: white;
}

.loading-screen {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  inset: 0;
}

.loading-bar-outer {
  min-width: 250px;
  width: 50%;
  max-width: 400px;
  height: 1rem;
  border: 2px solid white;
  padding: 1px;
  box-sizing: border-box;
  border-radius: 0.5rem;
  background-color: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(4px);
  margin-top: 1rem;
}

.loading-bar-inner {
  height: 100%;
  background-color: white;
  width: 0%;
  border-radius: 0.5rem;
}

.tip, .name {
  font-weight: bold;
  text-align: center;
}

.name {
  font-size: 2rem;
  margin-bottom: 2rem;
  animation: fadeInDown 1s both;
}

.tip {
  font-size: 0.875rem;
  margin-top: 2rem;
  animation: easeOutTip 1s forwards;
}

.content {
  opacity: 1;
  animation: fadeIn 1s forwards;
}

.navbar, .panel {
  display: flex;
  justify-content: center;
  padding: 1rem;
  gap: 1rem;
  border: 2px solid white;
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(9px);
  -webkit-backdrop-filter: blur(9px);
  margin: 2rem auto;
  width: 80%;
  max-width: 500px;
  animation: slideDown 0.8s ease-out forwards;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.navbar-item {
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem;
  display: block;
  white-space: nowrap;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.navbar-item:hover, .navbar-item:focus {
  background-color: rgba(255, 255, 255, 0.2);
  color: #FFF;
  border-radius: 5px;
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes easeOutTip {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideDown {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}